import React, {useEffect} from "react";
import Vision from "../Vision/Vision";
import TechnologyList from "../Technology";
import Contacts from "../Contacts/Contacts";
import ContactItem from "../Contacts/ContactItem";
import gsap from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);

const About = () => {
    useEffect(() => {
        let animatedItems = gsap.utils.toArray("[data-animated]");
        let anim = gsap.set(animatedItems, {opacity: 0, y: 100});
        ScrollTrigger.batch(animatedItems, {
            // interval: 0.1, // time window (in seconds) for batching to occur.
            // batchMax: 3, // maximum batch size (targets). Can be function-based for dynamic values
            onEnter: batch => gsap.to(batch, {opacity: 1, y: 0, duration: 1,  stagger: {each: 0.15, grid: [1, 3]},  overwrite: true}),
            onLeave: batch => gsap.set(batch, {opacity: 0, y: 100,duration: 1, overwrite: true}),
            onEnterBack: batch => gsap.to(batch, {opacity: 1, y: 0,duration: 1, stagger: 0.15, overwrite: true}),
            onLeaveBack: batch => gsap.set(batch, {opacity: 0, y: 100,duration: 1, overwrite: true}),
        });

        return () => {
            anim.kill();
        };
    }, []);
    return (
        <div id="about" className="about content flex gap-8 flex-col justify-end w-[51%] mt-24 ml-auto pr-10" >
            <section className="about__section" data-scroll-section data-animated>
                <h2 className="title" data-scroll data-scroll-speed="2" data-scroll-position="top">Short Bio</h2>
                <p data-scroll data-scroll-speed="2" data-scroll-position="top">Hey! I'm Nataly, a twenty-five years old Front-End Developer based in Ukraine, Ivano-Frankivsk.</p>
            </section>
            <section className="about__section pt-10" data-scroll-section data-animated>
                <h2 className="title" data-scroll data-scroll-speed="2" data-scroll-position="top">Vision</h2>
                <Vision/>
            </section>
            <section className="about__section pt-12" data-scroll-section  data-animated>
                <h2 className="title" data-scroll data-scroll-speed="3" data-scroll-position="bottom">Technical stack</h2>
                <TechnologyList/>
            </section>
            <section className="about__section text-center pt-12 pb-24" data-scroll-section  data-animated>
                <p className="text-xl" data-scroll data-scroll-speed="3" data-scroll-position="bottom">Would be glad to hear from you. Get in touch at: </p>
                <div data-scroll data-scroll-speed="3" data-scroll-position="bottom" className="mt-4">
                    <Contacts/>
                </div>
                <div data-scroll data-scroll-speed="3" data-scroll-position="bottom" className="mt-6">
                    <ContactItem/>
                </div>
            </section>
        </div>
    )
};

export default About;
