import React, {componentDidMount, useEffect, useRef, useState} from 'react';
import Stack from "../Stack/Stack";
import Header from "../Header/Header";
import "./Projects.scss";
import Contacts from "../Contacts/Contacts";
import Spinner from "../../components/Spinner/Spinner";

import {gsap} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import ContactItem from "../Contacts/ContactItem";

gsap.registerPlugin(ScrollTrigger);

const Projects = () => {
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        let stackItemsP = gsap.utils.toArray(".cta__text p");
        let anim1 = gsap.set(stackItemsP, {x: 100});
        ScrollTrigger.batch(stackItemsP, {
            interval: 0.1, // time window (in seconds) for batching to occur.
            batchMax: 3,
            delay: 5,
            // maximum batch size (targets). Can be function-based for dynamic values
            onEnter: batch => gsap.to(batch, {opacity: 1, x: 0, stagger: {each: 0.3, grid: [1, 3]}, overwrite: true}),
            onLeave: batch => gsap.set(batch, {opacity: 0, x: -100, overwrite: true}),
            onEnterBack: batch => gsap.to(batch, {opacity: 1, x: 0, stagger: 0.15, overwrite: true}),
            onLeaveBack: batch => gsap.set(batch, {opacity: 0, x: 100, overwrite: true}),
        },);

        let stackItemsDiv = gsap.utils.toArray(".cta__text div");
        let anim2 = gsap.set(stackItemsDiv, {opacity: 0});
        ScrollTrigger.batch(stackItemsDiv, {
            interval: 0.1, // time window (in seconds) for batching to occur.
            batchMax: 3,   // maximum batch size (targets). Can be function-based for dynamic values
            onEnter: batch => gsap.to(batch, {opacity: 1, stagger: {each: 0.15, grid: [1, 3]}, overwrite: true}),
            onLeave: batch => gsap.set(batch, {opacity: 0, overwrite: true}),
            onEnterBack: batch => gsap.to(batch, {opacity: 1, stagger: 0.15, overwrite: true}),
            onLeaveBack: batch => gsap.set(batch, {opacity: 0, overwrite: true}),
        });


        // scroll between stack items and cta text
        gsap.to(".cta__text", {
            scrollTrigger: {
                trigger: ".wrap",
                start: 'center 55%',
                toggleActions: 'play complete restart',
                scrub: .5,
                invalidateOnRefresh: true,
                anticipatePin: 1,
                snap: true,
            },
            ease: 'back'
        });


        return () => {
            anim1.kill();
            anim2.kill();
        };


    },);


    return (
        <>
            {loading ?
                (<Spinner setLoading={setLoading} loading={loading}/>) :
                (
                    <div className="wrap">
                        <Header className="projects" route="/" pageName="About"/>
                        <div className="projects__title-wrap">
                            <div className="projects__title">
                                <p>
                                    &nbsp; &nbsp;RECENT PROJECTS &nbsp;&nbsp;
                                    <span className="dot"></span> &nbsp;&nbsp;RECENT PROJECTS &nbsp;&nbsp;
                                    <span className="dot"></span> &nbsp;&nbsp;RECENT PROJECTS &nbsp;&nbsp;
                                    <span className="dot"></span> &nbsp;&nbsp;RECENT PROJECTS &nbsp;&nbsp;
                                    <span className="dot"></span> &nbsp;&nbsp;
                                </p>
                                <p>
                                    &nbsp; &nbsp;RECENT PROJECTS &nbsp;&nbsp;
                                    <span className="dot"></span> &nbsp;&nbsp;RECENT PROJECTS &nbsp;&nbsp;
                                    <span className="dot"></span> &nbsp;&nbsp;RECENT PROJECTS &nbsp;&nbsp;
                                    <span className="dot"></span> &nbsp;&nbsp;RECENT PROJECTS &nbsp;&nbsp;
                                    <span className="dot"></span> &nbsp;&nbsp;
                                </p>
                            </div>
                        </div>
                        <div id="projects">
                            <Stack/>
                        </div>
                        <div className="text-2xl color-white text-center relative bottom-0 transform translate-y-[-100%] uppercase cta__text hidden xl:block">
                            <p>Damn</p>
                            <p>You really</p>
                            <p>Like to</p>
                            <p>Scroll..</p>
                            <br/>
                            <p>Better say hi at</p>
                            <div className="lowercase italic mt-2 mb-4">
                                <Contacts/>
                            </div>
                            <div>
                                <ContactItem/>
                            </div>
                        </div>
                    </div>
                )
            }
        </>

    );
};
export default Projects;
