import React, {useEffect, useRef} from 'react';
import About from "../About";

import gsap from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";

const Home = () => {
    const containerRef = useRef(null);
    useEffect(() => {
        // gsap.fromTo(".home", {opacity: 0}, {opacity: 0.5, duration: 1});

        let animatedItems = gsap.utils.toArray("[data-img-animated]");
        let anim4 = gsap.set(animatedItems, {opacity: 0, scale: 0.9});
        ScrollTrigger.batch(animatedItems, {
            // interval: 0.1, // time window (in seconds) for batching to occur.
            // batchMax: 3, // maximum batch size (targets). Can be function-based for dynamic values
            onEnter: batch => gsap.to(batch, {
                opacity: 1,
                scale: 1,
                duration: 2,
                // stagger: {each: 0.15, grid: [1, 3]},
                overwrite: true
            }),
            onLeave: batch => gsap.set(batch, {opacity: 0, scale: 1, overwrite: true}),
            onEnterBack: batch => gsap.to(batch, {opacity: 1, stagger: 0.15, overwrite: true}),
            onLeaveBack: batch => gsap.set(batch, {opacity: 0, overwrite: true})
        })

        return () => {
            anim4.kill();
        };
    },);
    return (
        <div className="home">
            <div className="w-[40%] mr-auto fixed h-full" data-img-animated>
                <img src={`${process.env.PUBLIC_URL}/images/avatar.jpg`} alt=""/></div>
            <div className="main pt-10 pb-10">
                <About className="d-flex justify-end"/>
            </div>
        </div>
    );
};

export default Home;
