import React, {useEffect} from 'react';
import ContactItem from "../Contacts/ContactItem";

import "../../components/rollTextAnimation/rollTextAnimation.scss"
import {HashLink} from 'react-router-hash-link';


const Header = ({className = '', route, pageName}) => {

    useEffect(() => {
        document.querySelectorAll('.roll-text').forEach(button => button.innerHTML = '<div><span>' + button.textContent.trim().split('').join('</span><span>') + '</span></div>');
    }, );

    return (
        <div className={`header fixed top-0 left-0 right-0 z-50 p-1.5 ${className}`} id="header">
            {/*<ContactItem/>*/}
            <ul className="nav flex flex-row justify-end">
                <li className="nav__item">
                    <HashLink to={route} className="nav__item-link roll-text pl-4 pr-4 pt-3 pb-3">{pageName}</HashLink>
                </li>
            </ul>
        </div>
    );
};

export default Header;
