import React, {useEffect} from 'react';
import {contacts} from "../../data/contacts";
import "../../components/rollTextAnimation/rollTextAnimation.scss"


const ContactItem = ({className = ''}) => {
    useEffect(() => {
        document.querySelectorAll('.roll-text').forEach(button => button.innerHTML = '<div><span>' + button.textContent.trim().split('').join('</span><span>') + '</span></div>');
    }, );
    return (
        <ul className={`contacts_list flex gap-4 items-center justify-center ml-4 ${className}`}>
            {
                contacts.map((contact, id) => (
                    <li key={id}>
                        <a href={contact.url} className="roll-text hover:border-l-dark transition-all duration-500 ease-in-out cursor-pointer blank">
                            {contact.title}
                        </a>
                    </li>
                ))
            }
        </ul>
    );
};

export default ContactItem;
